
.title-team {
    text-align: center;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.15;
    color: #ea5f5f;
}

.team-list {
justify-content: start;
}

.our-team {
    display: inline-block;
    padding: 20px 0 20px;
    background-color: #252836;
    text-align: center;
    overflow: hidden;
    position: relative;
}

.our-team .picture {
    display: inline-block;
    height: 60px;
    width: 60px;
    margin-bottom: 10px;
    z-index: 1;
    position: relative;
}

.our-team .picture::before {
    content: "";
    width: 100%;
    height: 0;
    border-radius: 50%;
    background-color: #ea5f5f;
    position: absolute;
    bottom: 135%;
    right: 0;
    left: 0;
    opacity: 0.9;
    transform: scale(3);
    transition: all 0.3s linear 0s;
}

.our-team:hover .picture::before {
    height: 100%;
}

.our-team .picture::after {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #ea5f5f;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.our-team .picture img {
    width: 100%;
    height: auto;
    border-radius: 50%;
    transform: scale(1);
    transition: all 0.9s ease 0s;
}

.our-team:hover .picture img {
    box-shadow: 0 0 0 14px #f7f5ec;
    transform: scale(0.7);
}

.our-team .title {
    display: block;
    font-size: 12px;
    color: #4e5052;
    text-transform: capitalize;
}

.our-team .social {
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: #ea5f5f;
    position: absolute;
    bottom: -100px;
    left: 0;
    transition: all 0.5s ease 0s;
}

.our-team:hover .social {
    bottom: 0;
}

.our-team .social {
    display: inline-block;
}

.our-team .social a {
    display: block;
    padding: 10px;
    font-size: 10px;
    color: white;
    transition: all 0.3s ease 0s;
    text-decoration: none;
}

.our-team .social a:hover {
    color: #ea5f5f;
}
@media screen and (min-width:768px){
    .our-team {
            padding: 20px 0 40px;
    }
    .our-team .picture {
            height: 120px;
            width: 120px;
    }
} 



